import Vue from 'vue'
import Router from 'vue-router'

const Home = () => import('../components/home/home')

const Home1 = () => import('../components/home1/home1')

const CustomerService = () => import('../components/customer-service/customer-service')

const ShopCar = () => import('../components/shop-car/shop-car')

const My = () => import('../components/my/my')

const Curing = () => import('../components/curing/curing')

const Login = () => import('../components/login/login')

const Agreement = () => import('../components/agreement/agreement')

const Loading = () => import('../components/loading/loading')



Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      component:Login,
      
    },
    {
      path:'/home',
      component:Home,
      children:[
        

        {
          path:'',
          component:Home1,
          
        },
        {
          path:'curing',
          name:'curing',
          component:Curing,
          
        },
      ]
      
    },
    {
      path:'/customerservice',
      name:'customerservice',
      component:CustomerService,
      
    },
    {
      path:'/shopcar',
      name:'shopcar',
      component:ShopCar,
      
    },
    
    {
      path:'/my',
      name:'my',
      component:My,
      
    },
    {
      path:'/agreement',
      name:'agreement',
      component:Agreement,
      
    }
  ]
})



